@import url(https://fonts.googleapis.com/css?family=Playfair+Display|Work+Sans:400,500);
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Playfair Display', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 20px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Work Sans';
  font-weight: 500;
  font-size: 4.0rem;
  margin: 0;
}

#root {
  height: 100%;
}

@media only screen and (max-width: 767px) {
  body, html {
    font-size: 15px;
  }

  h1 {
    font-size: 3.2rem;
  }
}

@-webkit-keyframes fade-in {
  from { opacity: 0 }
  to { opacity: inherit }
}

@keyframes fade-in {
  from { opacity: 0 }
  to { opacity: inherit }
}

@-webkit-keyframes fade-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    opacity: inherit;
    -webkit-transform: none;
            transform: none;
  }
}

@keyframes fade-up {
  from {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }
  to {
    opacity: inherit;
    -webkit-transform: none;
            transform: none;
  }
}
.Illustrations {
  margin-top: 125px;
  -webkit-animation:  fade-in 1.0s;
          animation:  fade-in 1.0s;
  padding-bottom: 200px;
  width: 400px;
  max-width: 100%;
  text-align: center; 
  font-size: 13px;
  margin: 0 auto;
  padding-top: 200px;
}

.image {
  margin: 50px auto;
  -webkit-animation:  fade-in 1.0s;
          animation:  fade-in 1.0s;
  width: 400px;
  max-width: calc(100%-20px);
}

.spin {
  -webkit-animation: image-spin infinite 20s linear;
          animation: image-spin infinite 20s linear;
}

@-webkit-keyframes image-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes image-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
  
@media only screen and (max-width: 767px) {
  .image {
    max-width: 100%;
    height: auto;
  }
}

.title {
  font-size: 20px;
  font-family: "Playfair Display";
}
.Main {
    margin-top: 125px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    text-align: left;
}

.left {
    width: 100%;
    margin-top: 50px;
    -webkit-animation: fade-up 1.0s;
            animation: fade-up 1.0s;
}

.name {
    display: inline-block;
    margin-right: 10px;
}

.words {
    margin-top: 30px;
    margin-bottom: 40px;
    left: 0;
}

.underlined-button {
    color: #FCD962;
    text-decoration: none;
    font-family: 'Work Sans';
    -webkit-transition: 0.3s -webkit-filter;
    transition: 0.3s -webkit-filter;
    transition: 0.3s filter;
    transition: 0.3s filter, 0.3s -webkit-filter;
    position: relative;
    margin-bottom: 50px;
}

.underlined-button:hover {
    -webkit-filter: brightness(105%);
            filter: brightness(105%);
}

.underlined-button:after {
    content: "";
    position: absolute;
    height: 4px;
    left: 0;
    bottom: -5px;
    background: #FCD962;
    width: 35px;
    -webkit-transition: 0.3s width;
    transition: 0.3s width;
}

.underlined-button:hover:after {
    width: 100%;
}

.portrait {
    height: 100%;
    margin-left: 20px;
    width: 50%;
    -webkit-animation: fade-in 1.0s;
            animation: fade-in 1.0s;
}

@media only screen and (max-width: 1023px) {
    .portrait {
        margin-top: 50px;
        width: 40%;
    }
}

@media only screen and (max-width: 767px) {
    .Main {
        margin-top: 80px;
        display: block;
    }

    .portrait {
        margin-top: 40px;
        width: 100%;
    }

    .contact {
        display: none;
    }
}
.App {
  text-align: center;
  padding: 20px 10%;
  position: relative;
  height: 100%;
  max-width: 1000px;
  margin: auto;
  -webkit-animation: fade-in 1.0s;
          animation: fade-in 1.0s;
}

.logo {
  position: absolute;
  top: 30px;
  left: 45px;
  width: 125px;
  height: auto;
}

.logo:hover {
  -webkit-filter: brightness(105%);
          filter: brightness(105%);
}

.navlinks {
  position: absolute;
  top: 30px;
  right: 45px;
}

.navlink {
  font-size: 0.7rem;
  margin-left: 40px;
  text-decoration: none;
  color: #000000;
  font-family: 'Playfair Display';
  -webkit-transition: 0.1s color;
  transition: 0.1s color;
}

.navlink:hover {
  color: #FCD962;
}

.selected {
  color: #FCD962;
}

@media only screen and (max-width: 424px) {
  .logo {
    left: 30px;
  }

  .navlinks {
    right: 30px;
  }

  .navlink {
    margin-left: 30px;
  }
}
